            </p>
        </div>

        <div class="mt32">
            <button class="btn btn--white-green" @click="closeCooperationAccepted()"> !</button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'

    export default {
        name: "cooperationAccepted",
        components: {
            Popup
        },
        methods: {
            closeCooperationAccepted: function () {
                this.$store.commit('popups/close', {name: this.popups.cooperationAccepted.name});
                window.scroll(0,0);
                this.$router.push('/');
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

