<template>
    <div class="popup-info">
        <div>
        </div>
        
        <div class="text mt32">
            <div class="text-size-xxl text-color-red text-bolder">
                  {{ phone }}<br>
                 !
            </div>
            
            <p class="mt24">
                       <br>
                        .
            </p>
        </div>
        
        <div class="mt32">
            <span class="btn btn--white" @click="close">  </span>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'

    export default {
        name: "cooperationErrorPhone",
        props: {
            phone: ''
        },
        components: {
            Popup
        },
        methods: {
            close: function () {
                this.$emit('close');
                this.$store.commit('popups/close', {name: this.popups.cooperationErrorPhone.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

