<template>
    <div class="popup-info">
        <div>
        </div>
        
        <div class="text mt32">
            <div class="text-size-xxl text-color-red text-bolder">
                 ,<br>
                   !
            </div>
            
            <ul class="ul-disc-small mt24 text-left">
                <li>      {{ number_format(form.average.value) }}<span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,        <span v-html="form.discount.value"></span>%.</li>
                <li>      <span v-html="form.projectDiscount.value"></span>% (   <span v-html="form.projectDiscount.value - 3 > 0 ? form.projectDiscount.value - 3 : 0"></span>%   3%    ).</li>
                <li>,       {{ discountSumRetail }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,       {{ discountSum }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>.</li>
                <li><strong>           {{ discountSumUser }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>    .   !</strong></li>
            </ul>
        </div>
        
        <div class="mt32">
            <button class="btn btn--white-green" style="min-width: 328px; " @click="close">   </button>
            <router-link @click.native="close" style="min-width: 328px; " tag="button" to="/" class="btn btn--white-red  ml24" >    </router-link>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'
    import Tooltip from '@/components/Tooltip.vue';
    import {number_format} from '@/helpers/formatted.js';
    export default {
        name: "cooperationCancelVerySmallPrice",
        props: {
            form: {
                default: function () {
                    return {}
                }
            }
        },
        components: {
            Popup,
            Tooltip
        },
        methods: {
            close: function () {
                this.$emit('close');
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelVerySmallPrice.name});
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),

            discountSumRetail: function () {
                let discountSum = +this.form.average.value.split(' ').join('') * this.form.discount.value / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSumUser: function () {
                let discountSum = +this.form.average.value.split(' ').join('') * (this.form.projectDiscount.value - this.form.discount.value - 3) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },

            discountSum: function () {
                let discountSum = +this.form.average.value.split(' ').join('')   * (this.form.projectDiscount.value - 3) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
        }
    }
</script>

