                        sortScroll: 3
                    },
                    city: {
                        name: 'city',
                        value: '',
                        valueText: '',
                        error: false,
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 2
                    },
                    average: {
                        name: 'average',
                        value: '',
                        minValue: 1,
                        required: true,
                        error: false,
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 5
                    },
                    projectDiscount: {
