                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 7,
                        errorType: 0,
                    },
                    discount: {
                        name: 'discount',
                        value: '',
                        required: false,
                        error: false,
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 6
                    },
                    contactPerson: {
                        name: 'contactPerson',
                        value: '',
                        fields: {
                            lastName: {
                                name: 'lastName',
                                value: '',
                                delimiter: ' ',
                                placeholder: '',
                            },
                            firstName: {
                                name: 'firstName',
                                value: '',
                                delimiter: ' ',
                                placeholder: '',
                            },
                            secondName: {
                                name: 'secondName',
                                value: '',
                                delimiter: '',
                                placeholder: '',
                            },
                            position: {
                                name: 'position',
                                value: '',
                                postfix: ',',
                                delimiter: '',
                                placeholder: ', ',
                            },
                        },
                        required: true,
                        error: false,
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 8
                    },
                    company: {
                        name: 'company',
                        value: '',
                        required: true,
                        error: false,
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 1
                    },
                    phone: {
                        name: 'phone',
                        value: '',
                        required: true,
                        error: false,
                        errorType: 'empty',
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 9
                    },
                    email: {
                        name: 'email',
                        value: '',
                        required: true,
                        error: false,
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 10
                    },
                    site: {
                        name: 'site',
                        value: '',
                        required: true,
                        error: false,
                        errorText: '',
                        disabled: false,
                        focus:false,
                        sortScroll: 11
                    },
                    note: {
                        name: 'note',
