            }),
            discountSumRetail: function () {
                let discountSum = this.companyForm.average.value * this.companyForm.discount.value / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSumUser: function () {
                let discountSum = this.companyForm.average.value * (this.companyForm.projectDiscount.value - this.companyForm.discount.value - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSum: function () {
                let discountSum = this.companyForm.average.value * (this.companyForm.projectDiscount.value - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
        },
        mounted: function(){
            this.companyForm.city.value = this.currentCityIp;
        },
        methods: {
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            setTab(hash){
              this.$store.commit('actions/setCoopTab', hash);
            },
            changeTab: function(){
              scrollTo('#cooperation',true,0);
            },
            setFocus: function(ref) {
                if(!!this.$refs[ref]){
                    let input = this.$refs[ref].$el.getElementsByClassName('inp-s')[0];
                    if(input){

                        input.focus()
                        setTimeout(function () {
                            input.select()
                        },20)
                    }
                }
            },
            sendForm: function () {
                let validPrimary = this.validPrimary();
                if(validPrimary){
                    let fields = {};
                    for(let fieldIndex in this.companyForm) {
                        let field = this.companyForm[fieldIndex];
                        if(field.name ==='contactPerson'){
                            for(let fieldIndexPerson in field.fields) {
                                let fieldPerson = field.fields[fieldIndexPerson];
                                fields[fieldPerson.name] = fieldPerson.value.replace(',','').trim()
                            }
                        } else if(field.name === 'city') {
                            if(field.value.xmlId){
                                fields[field.name] = field.value.xmlId;
                            } else {
                                fields[field.name] = this.currentCityIp.xmlId
                            }
                        }
                        else if(field.name === 'average'){
                            fields[field.name] = field.value.split(' ').join('');
                        } else {
                            fields[field.name] = field.value.toString().trim();
                        }

                    }
                    fields.formName = 'cooperation';
                    this.buttonSave = 'load';
                    this.$store.dispatch('forms/sendForm', {params: fields}).then(()=>{
                        this.buttonSave = 'ok';
                        this.companyForm =  {
                            direction: {
                                name: 'direction',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    maxLength: 50,
                                    sortScroll: 3
                            },
                            city: {
                                name: 'city',
                                    value: '',
                                    valueText: '',
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 2
                            },
                            average: {
                                name: 'average',
                                    value: '',
                                    minValue: 1,
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 5
                            },
                            projectDiscount: {
                                name: 'projectDiscount',
                                    value: '',
                                    required: true,
                                    error: false,
                                    minValue: 1,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 7,
                                    errorType: 0,
                            },
                            discount: {
                                name: 'discount',
                                    value: '',
                                    required: false,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 6
                            },
                            contactPerson: {
                                name: 'contactPerson',
                                    value: '',
                                    fields: {
                                    lastName: {
                                        name: 'lastName',
                                            value: '',
                                            delimiter: ' ',
                                            placeholder: '',
                                    },
                                    firstName: {
                                        name: 'firstName',
                                            value: '',
                                            delimiter: ' ',
                                            placeholder: '',
                                    },
                                    secondName: {
                                        name: 'secondName',
                                            value: '',
                                            delimiter: '',
                                            placeholder: '',
                                    },
                                    position: {
                                        name: 'position',
                                            value: '',
                                            postfix: ',',
                                            delimiter: '',
                                            placeholder: ', ',
                                    },
                                },
                                required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 8
                            },
                            company: {
                                name: 'company',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 1
                            },
                            phone: {
                                name: 'phone',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorType: 'empty',
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 9
                            },
                            email: {
                                name: 'email',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 10
                            },
                            site: {
                                name: 'site',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 11
                            },
                            note: {
                                name: 'note',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 4
                            },
                            checkWhat:{
                                name: 'checkWhat',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 12
                            },
                            checkHow:{
                                name: 'checkHow',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 13
                            },
                            checkTerms:{
                                name: 'checkTerms',
                                    value: '',
                                    required: true,
                                    error: false,
                                    errorText: '',
                                    disabled: false,
                                    focus:false,
                                    sortScroll: 14
                            },
                        };
                        setTimeout(() => {
                            this.buttonSave = 'normal';
                        }, 300);
                    });
                }
            },
            setPersonInput: function (field) {
                if(field.value !== undefined && field.name !== undefined){
                    this.companyForm.contactPerson.fields[field.name].value = field.value;
                }
            },
            stepChange: function() {
                window.location.hash = 'step' + this.$refs.wizard.activeTabIndex; //
            },
            scrollToTab: function(tab){
              scrollTo('#tab-'+tab,true,0);
            },
            stepFinal: function() {
            },
            startStep: function() {
                let stepHash;

                if(window.location.hash !== '') {
                    stepHash = parseInt(window.location.hash.replace('#step',''), 10);
                    if(stepHash > 3) {
                        stepHash = 3;
                        window.location.hash = 'step' + stepHash;
                    }
                } else {
                    stepHash = 0;
                }

                return stepHash;
            },
            /*prevStep: function() {
                this.currentStep--;
                this.$nextTick(function () {
                    scrollTo('#cooperation-step-'+this.currentStep,true,-50);
                })
            },*/
            setCity: function(city) {
                this.companyForm.city.value = city;
                this.companyForm.city.valueText = city.name;
                this.$store.commit('popups/close', {name: this.popups.citySelectReturn.name});
            },
            validPrimary: function() {
                let error = false;
                let scrollToField = {
                    sortScroll: 99
                };

                let rEmail = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                let rSite = /^(http(s)?:\/\/)?([\w---]+\.)+[\w---]+(\/[\w--- ;,./?%&=]*)?$/;
                for(let fieldIndex in this.companyForm) {
                    let field = this.companyForm[fieldIndex];
                    let errorField = false;
                    field.error = false;
                    if(field.required) {
                        if(field.name ==='contactPerson') {
                            for(let fieldIndexPerson in field.fields) {
                                let fieldPerson = field.fields[fieldIndexPerson];
                                if(fieldPerson.value.length === 0){
                                    error = errorField = true;
                                }
                            }
                        } else {
                            if(field.name === 'projectDiscount' && field.value > 0) {
                                if(+field.value < +this.companyForm.discount.value){
                                    error = errorField = true;
                                    field.errorType = 1;
                                } else if(+field.value == +this.companyForm.discount.value){
                                    error = errorField = true;
                                    field.errorType = 3;
                                } else if(field.value - this.companyForm.discount.value < +this.options.minDiscount){
                                    error = errorField = true;
                                    field.errorType = 2;
                                }
                            }

                            if(field.minValue > 0 && +field.value <= field.minValue) {
                                error = errorField = true;
                            }
                            if(field.maxLength && field.value.length > field.maxLength) {
                                error = errorField = true;
                            }
                            if(field.value.length === 0){
                                error = errorField = true;
                            }
                            if(field.value === false){
                                error = errorField = true;
                            }
                            if(field.name === 'email' && !rEmail.test(field.value)) {
                                error = errorField = true;
                            }
                            if(field.name === 'site' && !rSite.test(field.value)) {
                                error = errorField = true;
                            }

                            if(field.name === 'phone') {
                                if(field.errorType === 'private' || field.errorType === 'order'){
                                    error = errorField = true;
                                } else {
                                    let phone = field.value.replace(/[^\+0-9]/g, '');
                                    if (phone.length < 12) {
                                        error = errorField = true;
                                        if(phone.length > 0){
                                            field.errorType = 'mobile';
                                        }
                                    } else if (phone[2] != '9') {
                                        error = errorField = true;
                                        field.errorType = 'mobile';
                                    }
                                }

                            }
                        }
                    }
                    setTimeout(() => {
                        field.error = errorField
                    }, 10);

                    if(errorField && scrollToField.sortScroll > field.sortScroll) {
                        scrollToField = field;
                    }
                }
                if(error && scrollToField) {
                    scrollTo('#cooperation-'+scrollToField.name,true,20);
                }
                if(!error){
                    let discount = this.companyForm.projectDiscount.value - this.companyForm.discount.value - 3;
                    if (+this.companyForm.average.value.split(' ').join('') > 0 && +this.companyForm.projectDiscount.value > 0) {
                        if ((+this.companyForm.average.value.split(' ').join('') * (discount) / 100) < this.options.cooperationProfit) {
                            this.openCooperationCancelSmallPriceOne();
                            error = true;
                        }
                    }
                }
                return !error;
            },
            validStep: function(step) {
                let error = false;
                let scrollToField = {
                    sortScroll: 99
                };


                let rEmail = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                let rSite = /^(http(s)?:\/\/)?([\w---]+\.)+[\w---]+(\/[\w--- ;,./?%&=]*)?$/;
                for(let fieldIndex in this.stepsField[step]) {
                    let field = this.companyForm[this.stepsField[step][fieldIndex]];
                    let errorField = false;
                    field.error = false;
                    if(field.required) {
                        if(field.name ==='contactPerson') {
                            for(let fieldIndexPerson in field.fields) {
                                let fieldPerson = field.fields[fieldIndexPerson];
                                if(fieldPerson.value.length === 0){
                                    error = errorField = true;
                                }
                            }
                        } else {
                            if(field.name === 'projectDiscount' && field.value > 0) {
                                if(+field.value < +this.companyForm.discount.value){
                                    error = errorField = true;
                                    field.errorType = 1;
                                } else if(+field.value == +this.companyForm.discount.value){
                                    error = errorField = true;
                                    field.errorType = 3;
                                } else if(field.value - this.companyForm.discount.value < +this.options.minDiscount){
                                    error = errorField = true;
                                    field.errorType = 2;
                                }
                            }
                            if(field.maxLength && field.value.length > field.maxLength) {
                                error = errorField = true;
                            }
                            if(field.minValue > 0 && +field.value <= field.minValue) {
                                error = errorField = true;
                            }
                            if(field.value.length === 0){
                                error = errorField = true;
                            }
                            if(field.value === false){
                                error = errorField = true;
                            }
                            if(field.name === 'email' && !rEmail.test(field.value)) {
                                error = errorField = true;
                            }
                            if(field.name === 'site' && !rSite.test(field.value)) {
                                error = errorField = true;
                            }

                            if(field.name === 'phone') {
                                if(field.errorType === 'private' || field.errorType === 'order'){
                                    error = errorField = true;
                                } else {
                                    let phone = field.value.replace(/[^\+0-9]/g, '');
                                    if (phone.length < 12) {
                                        error = errorField = true;
                                        if(phone.length > 0){
                                            field.errorType = 'mobile';
                                        }
                                    } else if (phone[2] != '9') {
                                        error = errorField = true;
                                        field.errorType = 'mobile';
                                    }
                                }

                            }
                        }
                    }
                    setTimeout(() => {
                        field.error = errorField
                    }, 10);

                    if(errorField && scrollToField.sortScroll > field.sortScroll) {
                        scrollToField = field;
                    }
                }
                if(error && scrollToField) {
                    scrollTo('#cooperation-'+scrollToField.name,true,20);
                }
                if(!error && step == 2){
                    let discount = this.companyForm.projectDiscount.value - this.companyForm.discount.value - 3;
                    if (+this.companyForm.average.value.split(' ').join('') > 0 && +this.companyForm.projectDiscount.value > 0) {
                        if ((+this.companyForm.average.value.split(' ').join('') * (discount) / 100) < this.options.cooperationProfit) {
                            this.openCooperationCancelSmallPriceOne();
                            error = true;
                        }
                    }
                }
                return !error;
            },
            checkPhone(field){
                field.focus = false;
                let phone = field.value.replace(/[^\+0-9]/g, '');
                let error = false;
                if (phone.length < 12) {
                    error = true;
                } else if (phone[2] != '9') {
                    error = true;
                }
                if(!error){
                    this.$store.dispatch('users/checkPhone', {phone: field.value}).then((e)=>{
                        if(e && e.code){
                            field.error = true;
                            field.errorType = e.code;
                            field.errorTypeStatus = e.statusOrder;
                        } else {
                            field.errorType = 'mobile';
                            field.errorTypeStatus = ''
                        }
                    });
                } else {
                    if(field.value.length > 0) {
                        field.errorType = 'mobile';
                        //field.error = true
                    } else {
                        field.errorType = 'empty';
                        //field.error = true
                    }
                }


            },
            removeError: function(field) {
                field.error = false;
                field.focus = true;
                if(field.name === 'projectDiscount'){
                    field.errorType = 0;
                }
            },
            blur(field){
                field.focus = false
                this.validFiled(field);
            },
            validFiled(field){
                if(field.value.length > 0){
                    let error = false;
                    let rEmail = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                    let rSite = /^(http(s)?:\/\/)?([\w---]+\.)+[\w---]+(\/[\w--- ;,./?%&=]*)?$/;
                    if(field.name === 'email' && !rEmail.test(field.value)) {
                        error = true;
                    }
                    if(field.name === 'site' && !rSite.test(field.value)) {
                        error = true;
                    }
                    field.error = error
                }

            },
            nextStep: function(){
                if(this.validStep(this.currentStep)) {
                    switch (this.currentStep) {
                        case 1:
                            this.currentStep = 2;
                            this.$nextTick(function () {
                                document.querySelector('.popup-content').scrollTo({top: 582, left: 0, behavior: 'smooth'});
                            })
                            break;
                        case 2:
                            this.currentStep = 3;
                            this.$nextTick(function () {
                                document.querySelector('.popup-content').scrollTo({top: 1043, left: 0, behavior: 'smooth'});
                            })
                            break;
                        case 3:
                            this.currentStep = 4;
                            this.$nextTick(function () {
                                this.scrollTop();
                            })

                            break;
                        case 4:
                            if(this.validPrimary()) {
                                this.currentStep = 5;
                                this.$nextTick(function () {
                                    this.scrollTop();
                                })
                            }
                            break;
                        default:
                            this.$refs.wizard.nextTab();
                            this.stepChange()
                            break;
                    }
                }

            },
            openCitySelect() {
                this.$store.commit('popups/open', {name: this.popups.citySelectReturn.name});
            },
            openCooperationAccepted: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationAccepted.name});
            },
            openCooperationErrorPhone: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationErrorPhone.name});
            },
            openCooperationCancelSmallPriceOne: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationCancelSmallPriceOne.name});
            },
            openCooperationCancelSmallPrice: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationCancelSmallPrice.name});
            },
            openCooperationCancelVerySmallPrice: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationCancelVerySmallPrice.name});
            },
            openCooperationWhat: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationWhat.name});
            },
            openCooperationHow: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationHow.name});
            },
            openCooperationPersonalData: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationPersonalData.name});
            },
            openCooperationTerms: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationTerms.name});
            },
            openCooperationCancelSmallPercent: function () {
                this.$store.commit('popups/open', {name: this.popups.cooperationCancelSmallPercent.name});
            },
            closeCooperationCancelSmallPercent: function () {
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelSmallPercent.name});
                this.setFocus('projectDiscount');
            },
            closeCooperationRequest: function () {
                this.$store.commit('popups/close', {name: this.popups.cooperationRequest.name});
            },
            closeCooperationPersonalData: function () {
                this.companyForm.checkTerms.value = true;
                this.$store.commit('popups/close', {name: this.popups.cooperationPersonalData.name});
            },
            scrollTop(){
                document.querySelector('.popup-content').scrollTo({top: 0, left: 0, behavior: 'smooth'});
            }
        }
    }
</script>

