<template>
    <div>
        <div class="popup__header mb">
            <div class="title" v-if="$route.name==='cooperation'">     </div>
            <div class="title" v-else>     </div>
        </div>


        <div class="popup-content mt24">
            <p class="text-size-ml">        
                <strong>{{ discountSumUser }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>. 
                <br>
                 ,   <b> {{options.cooperationProfit}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>.</b></p>
                
            <p class="text-size-ml mt24">   ?</p>
            
            <ul class="ul-disc-small mt16">
                <li>      {{number_format(average)}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                <li>      {{ discountSumRetail }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> (<span v-html="discount"></span>%)</li>
                <li>       <span v-html="projectDiscount"></span>% (  {{ options.ourDiscount }}%    )</li>
                <li>     {{ discountSum }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> (<span v-html="projectDiscount - options.ourDiscount"></span>%)</li>
                <li>       <b> {{ discountSumUser }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></b></li>
                <li>   ,       <b>  {{options.cooperationProfit}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></b></li>
            </ul>
        </div>
        
        <div class="mt32">
            <button class="btn btn--white-red" style="min-width: 328px; " @click="close()">   </button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'
    import {number_format} from '@/helpers/formatted';

    export default {
        name: "CooperationCancelSmallPriceOne",
        components: {
            Popup
        },
        props: {
            average: {
                default: function () {
                    return {}
                }
            },
            discount: {
                default: function () {
                    return {}
                }
            },
            projectDiscount: {
                default: function () {
                    return {}
                }
            },

        },
        methods: {
            close: function () {
                this.$emit('close');
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelSmallPriceOne.name});
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                options: state => state.options.items,
            }),
            discountSumRetail: function () {
                let discountSum = this.average * this.discount / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSumUser: function () {
                let discountSum = this.average * (this.projectDiscount - this.discount - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSum: function () {
                let discountSum = this.average * (this.projectDiscount - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
        }
    }
</script>

<style scoped>
    .ul-disc-small li {
        margin-top: 4px !important;
    }
</style>